@import '../theme';

@prefix-cls: ~'@{ra-lib-prefix}-table-row-draggable';

.sxTableRowDraggable tbody > tr {
    cursor: move;
}

.helper-element {
    display: table-row;
}

.helper-element td {
    border-bottom: 1px solid #f0f0f0;
    border-top: 1px solid #f0f0f0;
    padding: 8px;
    display: table-cell;
    vertical-align: center;
    background: #fff;
    width: 10000px;
    border-collapse: collapse;
}

.helper-element .@{ant-prefix}-form-item {
    margin-bottom: 0;
}

